import C from './constants';

export function updateConfig(configInfo) {
    return {
        type: C.UPDATE_CONFIG_INFO,
        payload: configInfo
    }
}

export function updateUserAuthData(configInfo) {
    return {
        type: C.UPDATE_USER_AUTH_DATA,
        payload: configInfo
    }
}

export function updateToggleMobileSideBar(status){
    if(status)
        return { type: C.SET_TOGGLE_MOBILE_SIDEBAR }
    else
        return { type: C.UNSET_TOGGLE_MOBILE_SIDEBAR }
}