import C from './constants';
import { combineReducers } from 'redux';

export const config = (state = {}, action) =>
    (action.type === C.UPDATE_CONFIG_INFO) ? action.payload : state;

export const userAuthData = (state = {}, action) =>
    (action.type === C.UPDATE_USER_AUTH_DATA) ? action.payload : state;


export const activeMobileSideBar = (state = false, action) => {

    switch (action.type) {

        case C.SET_TOGGLE_MOBILE_SIDEBAR:
            return true

        case C.UNSET_TOGGLE_MOBILE_SIDEBAR:
            return false

        default:
            return state
    }

}

export default combineReducers({
    config,
    userAuthData,
    activeMobileSideBar
    // userData: combineReducers({
    //     userFetching
    // })
})